.google-login-button {
  display: flex;
  align-items: center;
  background-color: white;
  color: #757575;
  border: 1px solid #ddd;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.google-login-button:hover {
  background-color: #4285f4;
  color: white;
}

.bc-logo {
  background-color: #ffffff; /* 로고 배경색 */
  border-radius: 5px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px; /* 로고 크기 */
  height: 48px;
  margin-right: 10px;
}

.google-logo {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.google-logo img {
  width: 100%;
  height: 100%;
}
